
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.product_logo{
  width: 60%
}
.mdl-layout__header-row{
  padding: 0 16px 0 55px !important;
}
.m-l-20{
  margin-left: 20px;
}
.m-r-15{
  margin-right: 15px;
}
.width-100-percent{
  width: 100% !important;
}
.horizontal-scroll{
  overflow-x: scroll;
}
.product_price{
  font-size: 20px;
  line-height: 24px;
  color: #fa505a;
}
.pink_color{
  color: #fa505a;
}
.product_section{
  margin-top: 70px;
}
.variant_type{
  width: 33%;
  padding: 0 4px;
  position: relative;
  height: 45px;
  background-color: #eee;
  margin: 0 2px;
}
.variant_containers{
  display: flex;
  text-align: center;
  justify-content: center;
  padding: 0 6px;
  margin-top: 10px;
}
.selected_variant{
  background-color: #fa505a !important;
  color: #fff !important;
}
.discounted_price{
  padding: 0 10px;
  font-size: 14px;
  text-decoration: line-through;
}
.discount_percent{
  font-size: 14px;
}
.flex-center{
  display: flex;
  align-items: center;
  justify-content: center;
}
.stone_quality{
  height: 70px;
  width: 70px;
  text-transform: capitalize;
  border: 1px solid #ddd;
  margin: 0 5px;
  font-size: 12px;
}
.selected_quality{
  border: 2px solid #fa505a !important;
}

.variant_details_container{
  margin-top: 20px;
}
.weight_wrapper{
  width: max-content;
}
.size_button{
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 1px solid;
  margin-right: 10px;
}
.size_wrapper{
  margin-top: 15px;
  width: max-content;
}
.add_to_cart_btn{
  background-color: #fa505a !important;
  color: #fff;
  font-weight: 600;
}
.add_to_cart_wrapper{
  margin-top: 20px;
}
.prop_name{
  font-weight: 600;
  margin: 20px 20px;
  font-size: 16px;
}
.prop_value{
  color: #666;
  font-size: 16px;
  text-transform: capitalize;
}
.m-l-10{
  margin-left: 10px;
}
.floating_cart_wrapper{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background: rgb(0,0,0,0.8);
}
.floating_cart_container{
  background: #fff;
  position: fixed;
  bottom: 0;
}
.padding-15{
  padding-left: 15px;
  padding-right: 15px;
}
.close_btn{
  font-size: 16px;
  float: right;
  margin-right: 10px;
  margin-top: 10px;
}
.none{
  display: none !important;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
